import React from 'react';
import classes from './FinalResults.module.css';

const FinalResults = (props) => {
  const percent = Math.floor(props.correctAnswers * 100 / props.totalItems);

  return (
    <div className={classes.resultsBox}>
      <div className={classes.title}>–û–∫–æ–Ω—á–∞—Ç–µ–ª—å–Ω—ã–π —Ä–µ–∑—É–ª—å—Ç–∞—Ç</div>
      <div>–ü—Ä–∞–≤–∏–ª—å–Ω—ã—Ö –æ—Ç–≤–µ—Ç–æ–≤: {props.correctAnswers}</div>
      <div>–ù–µ–≤–µ—Ä–Ω—ã—Ö –æ—Ç–≤–µ—Ç–æ–≤: {props.wrongAnswers}</div>
      <div className={classes.percent}>{percent}% –≤–µ—Ä–Ω—ã—Ö –æ—Ç–≤–µ—Ç–æ–≤</div>
      <button className={classes.btn} onClick={props.onRestart}>–ü–æ–≤—Ç–æ—Ä–∏—Ç—å</button>
    </div>
  )
}

export default FinalResults;